<template>
    <div class="form-container">
        <a-card title="Semina ya Fursa za Kiuchumi Nchini" :bordered="false">
            <h5 class="py-8">
                Kindly contact us at <span class="fg-blue">mipango-fursa@gmail.com</span>
                if you have any questions or difficulties filling the application form.
                The application deadline is April 10th at 11:59pm GMT.
            </h5>
            <a-form :model="formState" @finish="onFinish">
                <a-form-item label="First Name" name="firstName"
                    :rules="[{ required: true, message: 'Please input your first name!' }]">
                    <a-input v-model:value="formState.firstName" />
                </a-form-item>

                <a-form-item label="Last Name" name="lastName"
                    :rules="[{ required: true, message: 'Please input your last name!' }]">
                    <a-input v-model:value="formState.lastName" />
                </a-form-item>

                <a-form-item label="Phone" name="phoneNumber"
                    :rules="[{ required: true, message: 'Please input your phone number!' }]">
                    <a-input v-model:value="formState.phoneNumber" />
                </a-form-item>

                <a-form-item label="Email" name="email"
                    :rules="[{ required: true, message: 'Please input your email!' }]">
                    <a-input v-model:value="formState.email" />
                </a-form-item>

                <a-form-item label="Industry" name="industry"
                    :rules="[{ required: true, message: 'Please select your industry!' }]">
                    <a-select v-model:value="formState.industry" placeholder="Select your industry">
                        <a-select-option value="Technology">Technology</a-select-option>
                        <a-select-option value="Healthcare">Healthcare</a-select-option>
                        <a-select-option value="Finance">Finance</a-select-option>
                        <a-select-option value="Education">Education</a-select-option>
                        <a-select-option value="Manufacturing">Manufacturing</a-select-option>
                        <a-select-option value="Retail">Retail</a-select-option>
                        <a-select-option value="Agriculture">Agriculture</a-select-option>
                        <a-select-option value="Transportation">Transportation</a-select-option>
                        <a-select-option value="Energy">Energy</a-select-option>
                        <a-select-option value="Entertainment">Entertainment</a-select-option>
                        <a-select-option value="Other">Other</a-select-option>
                    </a-select>
                </a-form-item>

                <a-form-item>
                    <a-button type="primary" html-type="submit">Submit</a-button>
                </a-form-item>
            </a-form>
        </a-card>
    </div>
</template>

<script>
import { defineComponent, reactive } from "vue";
import { db } from "@/firebase"; // Import Firestore
import { collection, addDoc } from "firebase/firestore"; // Firestore functions

export default defineComponent({
    setup() {
        const formState = reactive({
            firstName: "",
            lastName: "",
            description: "",
            phoneNumber: "",
            email: "",
            industry: "",
        });

        const getDeviceInfo = () => {
            const userAgent = navigator.userAgent;
            let deviceType = "Unknown";

            if (/Mobile|Android|iPhone|iPad|iPod|Windows Phone/i.test(userAgent)) {
                deviceType = "Mobile";
            } else if (/Tablet|iPad/i.test(userAgent)) {
                deviceType = "Tablet";
            } else if (/Windows|Macintosh|Linux/i.test(userAgent)) {
                deviceType = "Desktop";
            }

            return deviceType;
        };

        const onFinish = async (values) => {
            try {
                // Add additional metadata
                const submissionData = {
                    ...values,
                    submissionDate: new Date().toISOString(), // Record date and time
                    deviceUsed: getDeviceInfo(), // Record device type
                };

                // Save data to Firestore
                const docRef = await addDoc(collection(db, "applications"), submissionData);
                console.log("Document written with ID: ", docRef.id);
                alert("Form submitted successfully!");
            } catch (error) {
                console.error("Error adding document: ", error);
                alert("Failed to submit form. Please try again.");
            }
        };

        return {
            formState,
            onFinish,
        };
    },
});
</script>

<style scoped>
.form-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

@media (max-width: 768px) {
    .form-container {
        max-width: 100%;
        padding: 10px;
    }
}
